import { Component, OnInit } from '@angular/core';
import { routerTransition } from '../../router.animations';

@Component({
    selector: 'app-dashboard',
    templateUrl: './dashboard.component.html',
    styleUrls: ['./dashboard.component.scss'],
    animations: [routerTransition()]
})
export class DashboardComponent implements OnInit {
    public alerts: Array<any> = [];
    public sliders: Array<any> = [];


    public masters: any = {
        title: 'Maters',
        icon: 'fa-indent',
        items: [
            {
                name: 'Product Category Master',
                route: '/inventory/product-category-master'
            },
            {
                name: 'Chart Of Items',
                route: '/inventory/item-master'
            },
            {
                name: 'Vendor Master',
                route: '/inventory/vendor'
            },
            {
                name: 'Unit Master',
                route: '/inventory/unit-master'
            },
            {
                name: 'Bill Of Material',
                route: '/inventory/bill-of-material'
            },
            {
                name: 'Bill Sundry',
                route: '/accounting/bill-sundry-master'
            },
            
        ]
    };

    public transaction: any = {
        title: 'Transaction',
        icon: 'fa-money',
        items: [
            {
                name: 'Store Items (Stock)',
                route: '/inventory/store-transaction'
            },
            {
                name: 'Requisition/Indents',
                route: '/inventory/requisition-detail-master'
            },
            {
                name: 'Import Good Receipt Note',
                route: '/inventory/te-direct-grn'
            },
            {
                name: 'Good Receipt Note',
                route: '/inventory/grn'
            },
            {
                name: 'Direct DRN',
                route: '/inventory/direct-grn'
            },
            {
                name: 'Purchase Order(Req)',
                route: '/inventory/po-requisition-detail-master'
            },
            {
                name: 'Material Issue',
                route: '/inventory/material-issue'
            },
            {
                name: 'Production Voucher',
                route: '/inventory/production-voucher'
            },
            {
                name: 'Stock Journal',
                route: '/inventory/stock-journal'
            },
            {
                name: 'Sales',
                route: '/inventory/sales'
            },
            {
                name: 'Store Transfer',
                route: '/inventory/store-transfer'
            },
            {
                name: 'Purchase Return',
                route: '/inventory/purchase-return'
            },
            {
                name: 'Issue Return',
                route: '/inventory/issue-return'
            },
            {
                name: 'Sales Return',
                route: '/inventory/sales-return'
            },
        ]
    };

    public reports: any = {
        title: 'Reports',
        icon: 'fa-book',
        items: [
            {
                name: 'Cost Of Materials',
                route: '/reports/cost-of-materials'
            },
            {
                name: 'Stock Report',
                route: '/reports/store-ledger-report'
            },
            {
                name: 'Stock Ledger',
                route: '/reports/stock-ledger-report'
            },
            {
                name: 'Opening Report',
                route: '/reports/opening-report'
            },
            {
                name: 'Current Fifo Report',
                route: '/reports/closing-fifo-report'
            },
            {
                name: 'Daily Store Log',
                route: '/reports/daily-store-log-report'
            },
            {
                name: 'Store Items (Stock)',
                route: '/inventory/store-transaction'
            },
            {
                name: 'Goods Receipt Report',
                route: '/reports/goods-receipt-report'
            },
            {
                name: 'Material Issue Report',
                route: '/reports/material-issue-report'
            },
            {
                name: 'PO (with Requisition)',
                route: '/reports/po-with-requisition-report'
            },
            {
                name: 'PO Direct',
                route: '/reports/po-direct'
            },
        ]
    };

    public setup: any = {
        title: 'Set Up',
        icon: 'fa-wrench',
        items: [
            {
                name: 'Estate',
                route: '/setup/estate"'
            },
            {
                name: 'Fiscal Year',
                route: '/inventory/fiscal-year'
            },
            {
                name: 'Inventory Setup',
                route: '/inventory/inventory-setup'
            },
            {
                name: 'Store Master',
                route: '/inventory/store'
            },
        ]
    };

    public utility: any = {
        title: 'Utility',
        icon: 'fa-cog',
        items: [
            {
                name: 'Manage Users',
                route: '/user-master/users'
            },
            {
                name: 'Manage Roles',
                route: '/user-master/roles'
            },
            {
                name: 'Manage Permissions',
                route: '/user-master/permissions'
            },
        ]
    }

    constructor() {
        this.sliders.push({
            imagePath: 'assets/images/slider1.jpg',
            label: 'First slide label',
            text: 'Nulla vitae elit libero, a pharetra augue mollis interdum.'
        }, {
            imagePath: 'assets/images/slider2.jpg',
            label: 'Second slide label',
            text: 'Lorem ipsum dolor sit amet, consectetur adipiscing elit.'
        }, {
            imagePath: 'assets/images/slider3.jpg',
            label: 'Third slide label',
            text: 'Praesent commodo cursus magna, vel scelerisque nisl consectetur.'
        });

        this.alerts.push({
            id: 1,
            type: 'success',
            message: `Lorem ipsum dolor sit amet, consectetur adipisicing elit.
                Voluptates est animi quibusdam praesentium quam, et perspiciatis,
                consectetur velit culpa molestias dignissimos
                voluptatum veritatis quod aliquam! Rerum placeat necessitatibus, vitae dolorum`
        }, {
            id: 2,
            type: 'warning',
            message: `Lorem ipsum dolor sit amet, consectetur adipisicing elit.
                Voluptates est animi quibusdam praesentium quam, et perspiciatis,
                consectetur velit culpa molestias dignissimos
                voluptatum veritatis quod aliquam! Rerum placeat necessitatibus, vitae dolorum`
        });
    }

    ngOnInit() {
    }

    public closeAlert(alert: any) {
        const index: number = this.alerts.indexOf(alert);
        this.alerts.splice(index, 1);
    }
}



